import React from "react";
import style from "../Styles/FirstPage.module.css";
import { BiMap } from "react-icons/bi";
import { FaPhoneAlt } from "react-icons/fa";
import { CgMail } from "react-icons/cg";
import { AiFillGithub } from "react-icons/ai";
import { AiFillLinkedin } from "react-icons/ai";
import { SiLeetcode } from "react-icons/si";

export default function FirstPage() {
  return (
    <div>
      <div className={style.top_part}>
        <img className={style.profile_image} src="Untitled.png" alt="img" />
        <h1 className={style.name}>Priyanka Poshala</h1>
        <h3 className={style.about}>Full stack web developer</h3>
        <div className={style.personal_details}>
          <div className={style.details2}>
            <span className={style.details_place}>
              <BiMap />
            </span>
            Hyderabad
          </div>
          <div className={style.details2}>
            {" "}
            <span className={style.details_place}>
              <FaPhoneAlt />
            </span>
            +919533912994
          </div>
          <div className={style.details2}>
            <span className={style.details_place}>
              {" "}
              <CgMail />
            </span>
            gmail
          </div>
        </div>

        <div className={style.socialmedia}>
          <div className={style.github}>
            <a
              className={style.github}
              href="https://github.com/masai-course/ti_091-priyanka"
              target="_blank"
            >
              <AiFillGithub />
            </a>
          </div>
          <div className={style.linkedin}>
            <a
              className={style.linkedin}
              href="https://www.linkedin.com/in/priyanka-poshala-9b621a1bb/"
              target="_blank"

            >
              {" "}
              <AiFillLinkedin />
            </a>
          </div>
          <div className={style.hacker}>
            <a
              className={style.hacker}
              href="https://leetcode.com/priyankaposh/"
              target="_blank"

            >
              <SiLeetcode />
            </a>
          </div>
        </div>
        <div className={style.description}>
          I'm a Hyderabad based Front End Developer and Open Source Contributor.
          <br />
          Coder who focuses on writing clean, elegant and efficient code.{" "}
        </div>
        <a href="https://drive.google.com/file/d/1_I0iUCILbtE09d_FdDNaGH7lSSAZ3fvs/view?usp=sharing" target="_blank">
          <button className={style.download}>Download resume</button>
        </a>
        <div className={style.line}></div>
      </div>
    </div>
  );
}
