import React from "react";
import style from "../Styles/techstack.module.css";
import { DiHtml5 } from "react-icons/di";
import { DiCss3 } from "react-icons/di";
import { IoLogoJavascript } from "react-icons/io";
import { BiGitMerge } from "react-icons/bi";
import { DiReact } from "react-icons/di";
import { FaNodeJs } from "react-icons/fa";
import { DiMongodb } from "react-icons/di";
import { SiRedux } from "react-icons/si";





export default function TechStack() {
  return (
    <div>
      <div className={style.techstack_wrap}>
        <button className={style.techstack_btn}>Tech Stack</button>
      </div>
      <div>
        <div className={style.techstack}>
          <div>
            <div className={style.html_pic}>
              <DiHtml5 />
            </div>
            <div className={style.html_tag}>HTML</div>
          </div>
          <div>
            <div className={style.html_pic}>
              <DiCss3 />
            </div>
            <div className={style.html_tag}>CSS3</div>
          </div>
          <div>
            <div className={style.html_pic}>
              <IoLogoJavascript />
            </div>
            <div className={style.html_tag}>JAVA SCRIPT</div>
          </div>
          <div>
            <div className={style.html_pic}>
              <BiGitMerge />
            </div>
            <div className={style.html_tag}>GIT</div>
          </div>
        </div>
        <div className={style.techstack}>
          <div>
            <div className={style.html_pic}>
              <DiReact />
            </div>
            <div className={style.html_tag}>REACT</div>
          </div>
          <div>
            <div className={style.html_pic}>
            <SiRedux />
            </div>
            <div className={style.html_tag}>REDUX</div>
          </div>
          <div>
            <div className={style.html_pic}>
              <DiMongodb />
            </div>
            <div className={style.html_tag}>Mongodb</div>
          </div>
          <div>
            <div className={style.html_pic}> 
              <FaNodeJs />
            </div>
            <div className={style.html_tag}>NODE JS</div>
          </div>
        </div>
      </div>
      <div className={style.line}></div>
    </div>
  );
}
