import React from "react";
import style from "../Styles/Cart.module.css"

export default function Card({img,head,para,aLink}) {
  return (
    <div className={style.card}>
      <img src={img} alt="" />
      <div className={style.card_head}>{head}</div>
      <div className={style.card_para}>{para}</div>
      <div>
       <div></div>
       <div></div>
       <div></div>
       <div></div>
       <div></div>
      </div>
      <div className={style.card_btn}>
        <div><a href={aLink} target="_blank"><button>Code</button></a></div>
        <div><a href=""><button>Website</button></a></div>
      </div>
    </div>
  );
}
